import React from "react";
import style from "../Auth.module.css";
import { assestsFeedUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import { useLogin } from "../hooks/use-login";
import { useLocation, useNavigate } from "react-router-dom";

const AuthExistingAccountCard = ({ account }) => {
  const getQuery = useLocation();
  const navigate = useNavigate();
  const { existingAccountLogin } = useLogin();
  const onClick = () => {
    if (getQuery.state?.landingPage?.appId) {
      existingAccountLogin(account, "LANDING_PAGE");
      if (getQuery.state?.landingPage?.isHostel === "hostel") {
        navigate("/q/hostel/joining", {
          state: {
            formAs: "STUDENT",
            openAs: "landing_page",
            appId: getQuery.state?.landingPage?.appId,
            hostelId: getQuery.state?.landingPage?.hostelId,
            verifyNumber: getQuery?.state?.phoneNumber,
          },
        });
      } else {
        navigate("/q/joining", {
          state: {
            formAs: "STUDENT",
            openAs: "landing_page",
            appId: getQuery.state?.landingPage?.appId,
            instituteId: getQuery.state?.landingPage?.instituteId,
            departmentId: getQuery.state?.landingPage?.departmentId,
            applicationName: getQuery.state?.landingPage?.applicationName,
            whichFormShow: "DEPARTMENT",
            verifyNumber: getQuery?.state?.phoneNumber,
          },
        });
      }
    } else existingAccountLogin(account, getQuery?.state?.accessFrom);
  };
  // console.info("account", getQuery?.state?.accessFrom);
  return (
    <div className={style.aea_card} onClick={onClick}>
      <img
        src={
          account?.user?.profilePhoto
            ? `${imageShowUrl}/${account?.user?.profilePhoto}`
            : `${assestsFeedUrl}/feed-user-avatar.svg`
        }
        alt="avatar icon"
      />
      <div className={style.aea_card_inner}>
        <h6>{account?.user?.userLegalName ?? ""}</h6>
        <p>{account?.user?.username ?? ""}</p>
      </div>
    </div>
  );
};

export default AuthExistingAccountCard;
